.ContactMe{
	min-height: 100vh;
	width: 100vw;
	height: auto;
	background: #111a28;
	display: flex;
	flex-direction: column;
	position: relative;
}
.MainContactMe{
	min-height: 90vh;
	height: auto;
	width: 100vw;
	border: 0px solid red;

	display: flex;
	justify-content: center;
	align-items: center;

}
.HeadingContactMe{
	width: 100vw;
	height: 10vh;
	display: flex;
	justify-content: center;
	align-items: center;
	border: 0px solid red;
}
